import React, {Component} from 'react';
import {Popup, Card} from "semantic-ui-react";

class DescriptionProcess extends Component {
    render() {
        const {process, processTime} = this.props;
        
        if (process !== undefined) {
            return (
                <Popup
                    size='large'
                    trigger={
                        <Card.Description>{"Текущий процесс(PID) : " + process.pid}</Card.Description>
                    }>
                    <Popup.Content>
                        <div>Память: {process.memory} кб</div>
                        <div>Оставшиеся время выполнения: {processTime}</div>
                        <div>Предпологаемое время выполнения: {process.time}</div>
                        <div>Приоритет: {process.priority}</div>
                        <div>Добавлен в очередь на такте: {process.startTime}</div>
                        <div>Статус: {process.status}</div>
                    </Popup.Content>
                </Popup>
            )
        } else {
            return (
                <Card.Description>Процессор простаивает</Card.Description>
            )
        }
    }
}

export default DescriptionProcess;


